* {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

li {
    list-style: none;
}

#common-nav {
    width: 30px;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    background: #202020;
}

#common-nav li {
    font-size: 14px;
    width: 100%;
    text-align: center;
    border-top: 1px solid black;
    border-bottom: 1px black;
    padding: 8px 0;
    color: white;
    cursor: pointer;
    letter-spacing: 2px;
}

#common-nav li a {
    color: white;
}

#common-nav li:hover {
    background: rgb(228, 85, 109);
}

#common-nav li:first-of-type {
    height: 40px;
}

#common-nav li:last-of-type {
    border-top: 1px white solid;
    position: absolute;
    bottom: 0;
    font-size: 12px;
}

.header {
    width: 100%;
    clear: both;
}

.header-nav {
    height: 33px;
    background: #fff;
    border-bottom: 1px solid #f0f0f0;
    margin: 0 auto;
}

.header-nav-wrap {
    width: 1200px;
    height: 100%;
    margin: 0 auto;
}

.header-nav-content {
    float: right;
    line-height: 33px;
    position: relative;
}

.header-nav-content a {
    font: 12px/1.6 tahoma, arial, sans-serif;
    font-weight: 300;
    margin: 0 10px;
    border-right: 1px solid #999;
    padding-right: 20px;
    cursor: pointer;
    color: #999;
}

.header-nav-content a span {
    color: #ffb7c4;
    font-size: 12px;
}

.header-nav-content a:last-of-type {
    position: relative;
    margin-right: 0;
    border-right: none;
    padding-right: 0;
}

.header-nav-content .header-abouts-box {
    position: absolute;
    top: 30px;
    right: -159px;
    width: 960px;
    height: 500px;
    background: yellow;
    display: none;
}

.header-wrap {
    width: 1200px;
    height: 100px;
    margin: 0 auto;
}

.header-wrap .tubiao img {
    width: 130px;
    height: 100px;
    float: left;
}

.header-wrap .mulu {
    float: left;
    padding-left: 32px;
    padding-top: 40px;
    position: relative;
    cursor: pointer;
}

.header-wrap .mulu-list {
    position: absolute;
    top: 80px;
    left: -230px;
    width: 100px;
    height: 300px;
    background: lightgoldenrodyellow;
    display: none;
}

.header-wrap .search-nav {
    float: left;
    margin-left: 90px;
}

.header-wrap .search-content {
    height: 44px;
    margin-top: 30px;
    border-radius: 8px;
}

.header-wrap .search-content .search {
    float: left;
    background: #f7f7f7;
}

.header-wrap .search-content form {
    margin-left: 20px;
}

.header-wrap .search-content input {
    margin-top: 10px;
    width: 450px;
    height: 25px;
    outline: none;
    border: none;
    background: #f7f7f7;
}

.header-wrap .search-content form .submit-btn {
    float: right;
    width: 60px;
    height: 44px;
    background: #999;
    border-radius: 0 8px 8px 0;
    cursor: pointer;
}

.header-wrap .search-content .submit-btn span {
    font-size: 35px;
    margin-left: 10px;
    line-height: 44px;
    color: white;
}

.header-wrap .search-content ul {
    float: left;
}

.header-wrap .search-content ul li {
    /* width: 35px; */
    height: 44px;
    margin: 0 12px;
    float: left;
    cursor: pointer;
}

.header-wrap .search-content ul li span {
    font-size: 28px;
}

.header-wrap .search-content ul li p {
    font-size: 12px;
}

.header-wrap .login {
    float: left;
    margin-left: 65px;
}

.header-wrap .login img {
    margin-top: 8px;
    float: left;
    width: 30px;
    height: 30px;
}

.header-wrap .login p {
    padding-top: 12px;
    margin-left: 5px;
    font-size: 15px;
    float: left;
}

.body_wrap {
    margin-top: 10px;
    width: 100%;
    height: 2200px;
}

.body_wrap .goods_nav {
    width: 1200px;
    height: 45px;
    margin: 0 auto;
    background: #f7f7f7;
    border: 1px solid rgb(216, 215, 215);
}

.body_wrap .goods_nav ul li {
    float: left;
    width: 50px;
    font-size: 16px;
    width: 50px;
    line-height: 45px;
    padding-left: 17px;
}

.body_wrap .goods_nav ul li:first-of-type {
    margin-left: 25px;
}

.body_wrap .goods_nav ul li.active {
    height: 42px;
    border: 1px solid rgb(216, 215, 215);
    border-top: 3px solid #ff4466;
    background: white;
}

.body_wrap .goods_about {
    width: 1200px;
    height: 45px;
    margin: 0 auto;
    background: white;
    border: 1px solid rgb(216, 215, 215);
}

.body_wrap .goods_about ul li:nth-of-type(1) span,
.body_wrap .goods_about ul li:nth-of-type(2) span,
.body_wrap .goods_about ul li:nth-of-type(3) span {
    width: 50px;
    border-right: 1px solid rgb(216, 215, 215);
    font-size: 13px;
    padding: 0 20px;
}

.body_wrap .goods_about ul li {
    float: left;
    line-height: 45px;
    cursor: pointer;
}

.body_wrap .goods_about ul p {
    float: left;
    margin: 12px -12px 0 6px;
}

.body_wrap .goods_about ul li input {
    border: none;
    width: 30px;
    outline: none;
    /* margin-left: -5px; */
}

.body_wrap .goods_about ul li:nth-of-type(4),
.body_wrap .goods_about ul li:nth-of-type(5) {
    margin-top: 10px;
    margin-left: 20px;
    border: 1px solid rgb(216, 215, 215);
    width: 50px;
    height: 25px;
    line-height: 25px;
}

.body_wrap .goods_about ul li:nth-of-type(6) {
    width: 40px;
    height: 25px;
    border: 1px solid rgb(216, 215, 215);
    font-size: 12px;
    line-height: 25px;
    text-align: center;
    margin: 10px 15px 0 15px;
}

.body_wrap .goods_about ul li:nth-of-type(7),
.body_wrap .goods_about ul li:nth-of-type(8),
.body_wrap .goods_about ul li:nth-of-type(9) {
    padding: 0 15px;
    height: 25px;
    border: 1px solid rgb(216, 215, 215);
    font-size: 12px;
    line-height: 25px;
    text-align: center;
    margin-top: 10px;
}

.body_wrap .goods_content {
    margin: 35px auto 0;
    width: 1200px;
}

.body_wrap .goods_content .goods {
    margin: 0 22.3px 25px 0;
    width: 220px;
    height: 380px;
    border: 1px solid #f2f2f2f3;
    margin-bottom: 25px;
    float: left;
    cursor: pointer;
    display: block;
    position: relative;
}

.body_wrap .goods_content .goods p:last-of-type {
    width: 210px;
    height: 25px;
    font-size: 16px;
    text-align: center;
    line-height: 25px;
    color: white;
    position: absolute;
    top: 275px;
    background: rgba(228, 85, 109, 0.8);
    display: none;
}

.body_wrap .goods_content .goods:hover {
    border: 1px solid #ff4466
}

.body_wrap .goods_content .goods:hover p:last-of-type {
    display: block;
}

.body_wrap .goods_content .goods:nth-of-type(5n) {
    margin-right: 0;
}

.body_wrap .goods_content .goods .box {
    width: 100%;
    height: 300px;
}

.body_wrap .goods_content .goods .box img {
    width: 100%;
    height: 100%;
}

.body_wrap .goods_content .goods>p {
    font-size: 13px;
    padding: 0 5px;
    color: rgb(83, 83, 83);
}

.body_wrap .goods_content .goods .price {
    margin: 10px 0 0 5px;
    font-size: 15px;
}

.body_wrap .goods_content .goods .price span {
    float: left;
    color: black;
}

.body_wrap .goods_content .goods .price span:nth-of-type(1),
.body_wrap .goods_content .goods .price span:nth-of-type(2) {
    font-weight: bolder;
}

.body_wrap .goods_content .goods .price span:nth-of-type(3) {
    margin-left: 5px;
}

.body_wrap .goods_content .goods .price span:nth-of-type(4) {
    text-decoration: line-through;
}

.body_wrap .goods_content .goods .price span:nth-of-type(5) {
    margin-left: 28px;
}

.body_wrap .goods_content .goods .price span:nth-of-type(5),
.body_wrap .goods_content .goods .price span:nth-of-type(6) {
    color: #999;
}

.footer {
    width: 100%;
    margin-top: 80px;
    background: #ff5ba0;
    .contentbox {
        width: 1200px;
        height: 488px;
        background-image: url(https://s10.mogucdn.com/mlcdn/c45406/181031_40flk9d5hkkjf44246248clfl77f1_1440x500.jpg);
        margin: 0 auto;
        position: relative;
        .brand {
            position: absolute;
            top: 50px;
            left: 515px;
            width: 170px;
            height: 50px;
            margin: 0 auto;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .content {
            position: absolute;
            top: 100px;
            width: 100%;
            height: 200px;
            margin-top: 40px;
            .top {
                display: flex;
                justify-content: flex-start;
                p {
                    padding-left: 30px;
                    width: 270px;
                    font-size: 15px;
                    color: white;
                }
            }
            .xian {
                width: 100%;
                p {
                    float: left;
                    margin: 5px 240px 5px 30px;
                    width: 30px;
                    height: 1px;
                    background: white;
                }
            }
            .items01 {
                width: 100%;
                margin-top: 35px;
                li {
                    float: left;
                    width: 300px;
                    height: 150px;
                    p {
                        height: 34px;
                        margin-left: 30px;
                        font-size: 13px;
                        color: white;
                    }
                    p:hover {
                        color: orange;
                    }
                }
            }
            .items02 {
                margin-left: 90px;
                width: 100%;
                margin-top: 25px;
                li {
                    float: left;
                    padding: 0 20px;
                    border-right: 1px solid white;
                    font-size: 13px;
                    color: white;
                }
                li:hover {
                    color: orange;
                }
                li:last-of-type {
                    border-right: none;
                    ;
                }
            }
        }
        .bottom {
            position: absolute;
            top: 370px;
            width: 100%;
            p {
                font-size: 13px;
                color: white;
                margin: 15px 0;
                text-align: center;
            }
        }
    }
}